<template>
    <div class="backgroundImg">
        <div class="setMain" v-if="Wsetpage=='main'">
            <h1 class="setMainTex"><b>[2024 2학기] 1학년<br>단어선택 리스트</b></h1>
            <p class="setText">세트</p>
            <div class="setListItem" v-for="file in files">
                <div class="setListWord">
                    <h3 class="setListWordText"><b>단어</b></h3>
                </div>
                <h3 class="setListText" @click="changePageWords(file.name)">{{ file.name }}</h3>
            </div>
        </div>
        <div class="setWords" v-if="Wsetpage=='words'">
            <div class="upperBg">
                <div class="wordDiv">
                    <p class="wordDivText">단어</p>
                </div>
                <p class="fileNameText"><b>{{ this.fileNameFor }}</b></p>
            </div>
            <div class="middleBg">

            </div>
            <div class="wordMean" v-for="word in fileForWord">
                <p class="wordMeanText">
                    ◼
                    <span class="wordMeanTextWord">
                        {{ word.word }}
                    </span>
                    <br>
                    <div class="wordMeanTextDiv" style="width:fit-content">
                        <p class="wordMeanTextMeaning" style="display:inline-block">
                            {{ word.meaning }}
                        </p>
                    </div>
                </p>
            </div>
            <div class="wordMenuBar">
                <div class="wordMenuButton">
                    <h2 class="wordMenuButtonText" @click="Wsetpage='word1'">암기</h2>
                </div>
                <div class="wordMenuButton">
                    <h2 class="wordMenuButtonText" @click="Wsetpage='word2'">리콜</h2>
                </div>
                <div class="wordMenuButton">
                    <h2 class="wordMenuButtonText" @click="Wsetpage='word3'">테스트</h2>
                </div>
            </div>
        </div>
        <div class="word1Page" v-if="Wsetpage=='word1'">
        <!-- !This is the first word page -->
            <div class="word1PageMiddle">

            </div>
            <div class="word1PageProb" v-for="word in arr" @click="changeWordProbT2">
                {{ word }}
            </div>
        </div>
        <div class="word2Page" v-if="Wsetpage=='word2'">
        <!-- !This is the second word page -->

        </div>
        <div class="word3Page" v-if="Wsetpage=='word3'">
        <!-- !This is the finall word page - chat page. -->

        </div>
    </div>
</template>
<script>
import fileList from '@/assets/words/files.json';
import firJSON from '@/assets/words/firstWords.json';
import Swal from 'sweetalert2';

const files = fileList;
const firstJSON = firJSON;
let fileForWord = firstJSON;
let fileNameFor = '중1 비상 테스트 단원';
let randNum;
let wordList = new Array();
let wordMeanings, wordWords = new Array();
let arr = new Array();

export default{
    name: 'SetPage',
    components: {

    },
    data(){
        return{
            files,
            Wsetpage: 'main',
            firstJSON,
            firJSON,
            fileForWord,
            fileNameFor,
            randNum,
            wordList,
            wordMeanings,
            wordWords,
            arr: ['시작하기'],
            rdwords: ['시작하기','시작하기','시작하기'],
            pages: ['중1 비상 8단원', '중1 비상 9단원', '중1 비상 10단원'],
        };
    },
    methods:{
        changePageWords: function(fileName){
            console.log(typeof firJSON);
            console.log(firJSON[0].word);
            console.log(firJSON.length);
            console.log(fileName);
            if(this.Wsetpage == 'main'){
                this.Wsetpage = 'words';
            } else if(this.Wsetpage == 'words'){
                this.Wsetpage = 'main';
            }
            if(fileName == '중1 비상 8단원'){
                this.fileForWord = this.firstJSON;
                console.log(fileForWord.length);
            }
            this.fileNameFor = fileName;
            if(this.Wsetpage == 'words'){
                Swal.fire({
                    icon: 'info',
                    title: '개발중인 페이지입니다.',
                    text: '메인 메뉴로 돌아가려면 새로고침하십시오.',
                    showCancelButton: false,
                    confirmButtonText: '예', 
                    confirmButtonColor: '#429f50',
                })
            }
        },
        /** //! Error Here! : Undefined in array - FIX THIS!!!! */
        getRandomElement(arr){
            console.log(arr[Math.floor(Math.random() * arr.length)]);
            return arr[Math.floor(Math.random() * arr.length)];
        },
        fileListForWord(){
            let i;
            for(i=0;i<fileForWord.length;i++){
                console.log(fileForWord.length);
                this.wordList[i] = this.fileForWord.meaning;
                console.log(fileForWord[i].meaning);
            }
            console.log(typeof wordList);
            console.log(wordList.length);
            this.changeWordProbT2();
        },
        changeWordsProb: function(){
            let j = 0;
            let im = [];
            let Index = 0;
            let i = 0;

            for(i in this.fileForWord){
                im[j] = i.meaning;
                j = j + 1;
            }

            for(j=0; j<3; j++){
                this.rdwords[j] = this.getRandomElement(im);
                console.log(this.getRandomElement(im));
                Index = im.indexOf(this.rdwords[i]);
                if(Index > -1){
                    im.splice(Index, 1);
                }
                console.log(this.rdwords[j]);
            }
        },
        changeWordProbT2(){
            let arr2 = new Array();
            let i, a, count;
            count = 0;
            this.arr.splice(0,this.arr.length);
            while(2>1){
                a = Math.floor(Math.random() * fileForWord.length);
                if(arr2.includes(a) | a<0){
                    continue;
                } else{
                    this.arr.push(fileForWord[a].meaning);
                    arr2[count] = a;
                    console.log(arr2[count]);
                    console.log(this.arr[count]);
                    count+=1;
                    if(count >= 3){
                        console.log(this.arr);
                        break;
                    }
                }
            }
        }

    },
}
</script>
